import axios from 'axios';

const hostAPI = 'https://eemcompact.com';

export const request = async ({ url = '', method = 'get', params, data, headers = {} }) => {
  try {
    const result = await axios({
      url: `${hostAPI}${request.prefix || ''}${url}`,
      method,
      data,
      params,
      headers: {
        Accept: 'application/json',
        'Content-Type': 'application/json',
        'Access-Control-Allow-Origin': '*',
        ...headers,
      },
    });
    return result;
  } catch (err) {
    console.log('err', err);
    const { response } = err;
    if (response && response.status === 403) {
      window.localStorage.clear();
      window.location.href = '/#/login';
    }
    return response;
  }
};

export const getHeaderAuthorization = token => {
  const t = !token ? window.localStorage.getItem('app.token') : token;
  return { authorization: `Bearer ${t}` };
};



